import React from "react";
import { AdminCard } from "../../../components";
import { FaRegUserCircle } from "react-icons/fa";

import { BsBoxArrowLeft, BsBoxArrowInRight } from "react-icons/bs";
import "../../../shared/dashboard.css";
import Chart from "../../../components/Chart";
import { useGetAbsentUsers, useGetAttendance, useGetUsers } from "../hooks";
import Layout from "../../../layout";
import ActivityList from "../../../components/ActivityList";

const Index = () => {
  const { data } = useGetUsers();
  const { data: absentStaff } = useGetAbsentUsers();
  const { data: attendance } = useGetAttendance();

  // data with no repetition
  const uniqueAttendance = (objects) => {
    const uniqueEmailObjects = {};

    // Iterate over the list of objects
    for (const obj of objects) {
      const email = obj.email;
      // Store the object in the uniqueEmailObjects object using email as the key
      // This will ensure that only one instance is stored for each unique email
      if (!uniqueEmailObjects[email]) {
        uniqueEmailObjects[email] = obj;
      }
    }

    // Get the unique objects as an array
    const uniqueObjects = Object.values(uniqueEmailObjects);
    return uniqueObjects;
  };

  //from array of attendances get today atendance

  function checkAndCreateTodayAttendance() {
    // const today = "2023-05-12T09:21:17";
    // const formattedDate = today.split('T')[0]
    const today = new Date().toISOString().split("T")[0]; // Get today's date in YYYY-MM-DD format
    const todayAttendance = [];

    attendance?.forEach((item) => {
      const itemDate = item.created_at.split("T")[0]; // Extract the date from the "create" property
      if (itemDate === today) {
        todayAttendance.push(item); // Add the matching object to the todayAttendance array
      }
    });

    return todayAttendance;
  }

  //card data
  const result = uniqueAttendance(checkAndCreateTodayAttendance());

  const checkIn = result?.filter((x) => x.is_signed_in === true);
  const checkOut = result?.filter((x) => x.is_signed_out === true);
  const lateCheckIn = result?.filter((x) => x.is_signed_in_late === true);
  const earlyCheckOut = result?.filter((x) => x.is_signed_out_early === true);
  const overtimeCheckOut = result?.filter(
    (x) => x.is_signed_out_overtime === true
  );

  //get the last 4 recent activity

  function getLastFourElements(result) {
    if (result?.length < 4) {
      return result;
    }
    const length = result?.length;
    const lastFourArray = result?.slice(length - 4, length);
    return lastFourArray;
  }
  const lastFour = getLastFourElements(result);

  console.log(lastFour, "log");

  return (
    <Layout name="Dashboard" title="Dashboard">
      <div className="cardFlex">
        <AdminCard
          title={"Total Users"}
          count={data?.length}
          Icon={FaRegUserCircle}
          color="cyan"
          colorInner="lightCyan"
        />
        <AdminCard
          title={"Total Check-In"}
          count={checkIn?.length ? checkIn.length : 0}
          Icon={BsBoxArrowInRight}
          color="gold"
          colorInner="lightGold"
        />
        <AdminCard
          title={"Total Check-Out"}
          count={checkOut?.length ? checkOut.length : 0}
          Icon={BsBoxArrowLeft}
          color="crimson"
          colorInner="lightCrimson"
        />

        <AdminCard
          title={"Late Check-In"}
          count={lateCheckIn?.length ? lateCheckIn.length : 0}
          Icon={BsBoxArrowInRight}
          color="purple"
          colorInner="lightPurple"
        />
        <AdminCard
          title={"Early Check-Out"}
          count={earlyCheckOut?.length ? earlyCheckOut.length : 0}
          Icon={BsBoxArrowLeft}
          color="gold"
          colorInner="lightGold"
        />
        <AdminCard
          title={"Overtime Check-Out"}
          count={overtimeCheckOut?.length ? overtimeCheckOut.length : 0}
          Icon={BsBoxArrowLeft}
          color="crimson"
          colorInner="lightCrimson"
        />

        <AdminCard
          title={"Absent Staff"}
          count={absentStaff?.length ? absentStaff.length : 0}
          Icon={FaRegUserCircle}
          color="cyan"
          colorInner="lightCyan"
        />
      </div>
      <div className="chart">
        <div className="chartContainer">
          <div className="chartItems">
            <Chart
              total={data?.length}
              latecomer={20}
              checkin={checkIn?.length ? checkIn.length : 0}
              checkout={checkOut?.length ? checkOut.length : 0}
              late_checkin={lateCheckIn?.length ? lateCheckIn.length : 0}
              early_checkout={earlyCheckOut?.length ? earlyCheckOut.length : 0}
              overtime_checkout={
                overtimeCheckOut?.length ? overtimeCheckOut.length : 0
              }
            />
          </div>
        </div>
        <div className="activityContainer">
          <h2>Recent Activity</h2>

          <div className="activity">
            {lastFour.map((item, i) => (
              <ActivityList
                user={item.user?.last_name}
                time={item.created_at?.replace("T", " at ").slice(0, -10)}
                logout={item?.is_signed_out}
              />
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Index;
